.content-wrapper {
    max-width: 600px;
    height: 100%;
    margin: 20px auto;
    font-size: 3rem;
    line-height: 3.5rem;
    box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
}

.header__title {
    width: 100%;
    display: flex;
    justify-content: center;
    margin: 30px auto;
    color: rgba(255, 105, 105, 0.415);
    font-size: 10rem;
    line-height: 11rem;
}
